#matter-sidenav {
    background-color: transparent;
    height: 100%;

    #sidebar-lock-button {
        .absolute();
        z-index: 25;
        .topRight(30px, 6px);
    }

    #matter-menu-group-wrapper {
        width: 64px;
        padding: 0;
        height: 100%;
        background-color: @base_dark;
        .boxShadow();

        #nav-user-profile {
            .nopadding();
            .nomargin();
            .gradient-soft();

            button {
                .nopadding();
                width: 100%;
                height: 100%;
                .nomargin();

                img {
                    .square(48px);
                    .rounded();
                    .absolute();
                    .spread();
                    margin: auto;
                }
            }

            &:after {
                display: none;
            }
        }

        matter-menu-item {
            .cursor();
            .block();
            width: 64px;
            height: 64px;
            text-align: center;
            line-height: 64px;
            .relative();
            overflow: hidden;
            border-bottom: 1px solid rgba(0, 0, 0, .1);
            border-top: 1px solid rgba(255, 255, 255, .05);
            box-sizing: border-box;

            &:last-child {
                border-bottom: 0;
            }

            matter-notif-counter {
                .absolute();
                .topRight(10px, 10px);
                height: 14px;
                width: 14px;
                line-height: 14px;
                background: linear-gradient(45deg, #ff598f 0%, #ff6f5a 100%);
                border-radius: 50%;
                font-size: 10px;
                font-weight: 800;
                color: white;
            }

            &:after {
                content: 'after';
                text-indent: -99999px;
                .absolute();
                right: -4px;
                top: 0;
                width: 4px;
                height: 100%;
                .gradient-soft();
                .transition(transform .2s ease);
            }

            md-icon {
                margin: auto;
            }

            &.active {
                .inner-shadow(0 0 6px rgba(0, 0, 0, 0.25));
                background-color: rgba(0, 0, 0, .1);
                border: 0;

                &:after {
                    transform: translate3d(-4px, 0, 0);
                }
            }

            &:hover {
                background-color: rgba(255, 255, 255, .03);
            }
        }
    }

    #matter-submenu-wrapper {
        .relative();
        position: 20;
        width: 240px;
        background-color: transparent;

        md-list {
            width: 100%;
            .absolute();

            h3 {
                letter-spacing: 1px;
            }

            h3, md-list-item {
                .transition();
                opacity: 1;
                .translate3d(0, 0, 0);

                p {
                    font-size: 15px;
                    text-transform: capitalize;
                    font-weight: 300;
                }

                &:nth-child(2) {
                    .transition-delay(.1s);
                }

                &:nth-child(3) {
                    .transition-delay(.2s);
                }

                &:nth-child(4) {
                    .transition-delay(.3s);
                }

                &:nth-child(5) {
                    .transition-delay(.4s);
                }

                &:nth-child(6) {
                    .transition-delay(.5s);
                }

                &:nth-child(7) {
                    .transition-delay(.6s);
                }

                &:nth-child(8) {
                    .transition-delay(.7s);
                }

                &:nth-child(9) {
                    .transition-delay(.8s);
                }

                &:nth-child(10) {
                    .transition-delay(.9s);
                }
            }

            md-list-item {

                matter-submenu-notif {
                    .absolute();
                    right: 16px;
                    top: 0;
                    bottom: 0;
                    margin: auto;
                    height: 16px;
                    width: 26px;
                    border: 1px solid #969696;
                    color: #969696;
                    font-size: 12px;
                    line-height: 16px;
                    text-align: center;
                    border-radius: 12px;
                    font-weight: 500;
                }
            }

            &.ng-hide {
                display: block !important;
                pointer-events: none;

                h3, md-list-item {
                    opacity: 0;
                    .translate3d(-40px, 0, 0);
                }
            }
        }
    }
}
