@base_dark: #323232;

html {

    * {
        outline: 0;
    }

    body {
        height: 100vh;
        width: 100vw;
        overflow: hidden;
        .gradient-linear(#614385, #516395);

        matter-app-wrapper {
            //height: 90%;
            //width: 90%;
            .spread();
            margin: auto;
            .boxShadow();
            overflow: hidden;
            background-color: white;
            border-radius: 2px;

            #matter-sidenav {

            }

            #md-content-right {
                .absolute();
                z-index: 50;
                left: 64px;
                top: 0;
                background-color: white;
                width: calc(~"100% - 64px");
                height: 100%;
                transition: all .22s ease;

                #app-view {
                    .absolute();
                    .spread();

                    > md-content {
                        height: 100%;
                        width: 100%;

                        > md-toolbar {
                            background-color: #f2f2f2;
                            color: #323232;
                            border-bottom: 1px solid #f1f1f1;

                            &.md-toolbar-header {
                                min-height: 128px;
                                max-height: 128px;

                                md-list {
                                    padding: 0;

                                    .matter-table-header {
                                        .noselect();
                                        font-size: 12px;
                                        text-transform: uppercase;
                                        font-weight: 600;
                                        color: #646464;
                                        letter-spacing: .75px;

                                        matter-button-wrapper {
                                            .absolute();
                                            right: 16px;
                                        }

                                        p {
                                            margin: 0;
                                        }
                                    }
                                }
                            }

                            &.md-toolbar-search {
                                min-height: 196px;
                                max-height: 196px;
                            }

                            .toolbar-tools {
                                padding: 0 16px;
                                padding-bottom: 12px;

                                h2 {
                                    margin: 0;
                                    font-size: 24px;
                                    font-weight: 200;
                                    line-height: 30px;
                                }

                                .batch-menu {

                                    md-menu {
                                        margin: 10px auto;
                                    }

                                    > .toolbar-button {
                                        .transition(all .11s ease);

                                        &:nth-last-child(2) {
                                            .transition-delay(.3s);
                                        }

                                        &:nth-last-child(3) {
                                            .transition-delay(.6s);
                                        }
                                    }
                                }
                            }
                        }

                        .matter-data-container {
                            position: relative;

                            md-list {

                                md-list-item {

                                    p {
                                        .ellipsis();
                                        padding-right: 24px;
                                    }

                                    md-menu {
                                        position: absolute;
                                        top: 0;
                                        right: 16px;
                                        bottom: 0;
                                        margin: auto;
                                    }

                                    &:not(.matter-table-header):hover {
                                        background-color: #fafafa;
                                    }
                                }
                            }
                        }
                    }
                }
            }
        }

        &.aside {

            .nav-group-tooltip {
                display: none;
            }

            #matter-sidenav {

                #matter-menu-group-wrapper {

                    matter-menu-item {

                    }
                }
            }

            #md-content-right {
                .boxShadow();
                transform: translate3d(240px, 0, 0) scale(.95);
            }
        }

        &.sidelocked {

            #md-content-right {
                width: calc(~"100% - 304px");
                .translate3d(240px, 0, 0);
                border-left: 1px solid #f1f1f1;
                box-shadow: none;
            }
        }
    }
}
