#matter-page-dashboard {

    matter-graph-container {
        height: 320px;
        .relative();
        .block();
        .gradient-linear(rgb(76,184,196), rgb(60,211,173));

        matter-graph-control {
            .absolute();
            .topRight(16px, 16px);

            button {
                margin-left: 16px;

                &:hover {
                    background-color: rgba(255, 255, 255, 0.35);
                }
            }
        }

        matter-graph-pole {
            display: block;
            width: 2px;
            background-color: white;
            position: absolute;
            bottom: 0;

            &:after {
                .absolute();
                left: -5px;
                top: -6px;
                content: 'after';
                text-indent: -99999px;
                border: 2px solid white;
                width: 8px;
                height: 8px;
                .rounded();
            }
        }
    }

    #dashboard-quick-data {
        padding: 32px 0;

        .dashboard-quick-data-wrapper {
            padding: 0 32px;
            border-right: 1px solid rgba(0, 0, 0, .1);
            overflow-y: hidden;

            &:nth-child(4) {
                border: 0;
            }

            h2 {
                font-size: 40px;
                font-weight: 100;
                margin: 0;
                margin-bottom: 4px;
            }

            p {
                margin: 0;
                font-size: 14px;
            }

            .dashboard-quick-graph-wrapper {
                height: 48px;
                width: 100%;
                margin-top: 12px;
                .relative();
                border-bottom: 1px solid #7DD5E1;

                matter-graph-pole {
                    display: block;
                    width: 2px;
                    background-color: #7DD5E1;
                    position: absolute;
                    bottom: 0;

                    &:after {
                        .absolute();
                        left: -3px;
                        top: -3px;
                        content: 'after';
                        text-indent: -99999px;
                        border: 2px solid #7DD5E1;
                        width: 4px;
                        height: 4px;
                        .rounded();
                    }
                }
            }
        }
    }
}

