}

#matter-page-chat {
    width: 100%;
    height: 100%;

    #messages-contact-list {
        background-color: #fafafa;
        border-right: 1px solid #f1f1f1;
        width: 240px;
    }

    #chat-content-wrapper {

        md-toolbar {
            .transition(all .11s ease);

            &.ng-hide {
                display: block !important;
                transform: translate3d(0, -128px, 0);
            }
        }

        #chat-container {
            background: linear-gradient(to bottom, #ede9e4 0%, #ede9e4 20%, #e5ddd5 100%);

            #chat-background {
                background-image: url('/assets/img/whatsapp.png');
                opacity: .05;
                .spread();
            }

            #chat-empty {
                .spread();
                background-color: white;
            }
        }

        #messages-input-wrapper {
            background-color: #fafafa;

            md-input-container {
                margin: 0 24px;
                margin-top: 24px;
            }
        }
    }
}
