@ncolor: #ff598f;

#nprogress {
    .absolute();
    z-index: 100;

    .bar {
        .gradient-soft();
        height: 4px;
    }

    .peg {
        box-shadow: 0 0 10px @ncolor, 0 0 5px @ncolor;
    }

    .spinner-icon {
        border-top-color: @ncolor;
        border-left-color: @ncolor;
    }
}

md-checkbox {

    .md-icon {
        border-width: 1px;
        .rounded();

        &:after {
            transform: rotate(45deg) scale(.8);
        }
    }

    &.md-checked {

        .md-icon {
            .gradient-soft();

            &:after {
                transform: rotate(45deg) scale(.8);
            }
        }
    }
}

button {

    &.matter-button {
        border: 2px solid rgba(255, 255, 255, 0.65);
        border-radius: 14px;
        height: 28px;
        line-height: 26px;
        box-sizing: border-box;
        font-size: 12px;
        color: white;
        background: transparent;
        font-weight: 700;
        padding: 0 12px;
    }
}

md-icon {

    &.md-icon-dark {

        svg {
            fill: #323232;
        }
    }
}

md-menu-content {

    md-menu-item {

        a {
            line-height: 48px !important;
        }
    }
}

md-input-container.md-default-theme:not(.md-input-invalid).md-input-focused .md-input,
md-input-container:not(.md-input-invalid).md-input-focused .md-input {
    border-color: #ff598f;
}

md-list {

    > h3 {
        padding: 0 24px;
        padding-top: 24px;
        font-size: 13px;
        color: #646464;
        text-transform: uppercase;
    }
}

md-toolbar.md-tall {
    background-color: #fafafa;
    color: #323232;
    border-bottom: 1px solid #f1f1f1;

    .md-toolbar-tools {
        //padding: 12px 16px !important;

        h2 {
        }
    }
}

.md-toolbar-search-wrapper {
    .boxShadow();

    md-input-container {
        margin: 0;
        padding: 0;

        md-select {
            width: 140px;
            background: #323232;
            color: white !important;
            padding: 5px 8px;
            font-size: 12px;
            text-transform: uppercase;
            font-weight: 600;

            md-select-value {
                .translate3d(0, -2px, 0);
                letter-spacing: 1px;
                border: 0 !important;
            }
        }
    }

    input {
        font-size: 14px;
        padding: 11px;
        border: 0;
    }
}

md-select-menu.md-default-theme md-option[selected], md-select-menu md-option[selected] {
    color: rgb(255, 108, 119) !important;
    font-weight: 700;
}

.md-toolbar-search-wrapper md-input-container md-select:not([disabled]):focus .md-select-value {
    color: white;
    padding-bottom: 1px;
}

md-dialog {
    border-radius: 2px;

    &.md-default-theme {
        border-radius: 2px;
    }
}
