body {

    [ng-view] {
        -webkit-backface-visibility: hidden;
        //-webkit-perspective: 1000;
        //.translate3d(0, 0, 0);
        transition: all .33s ease;

        &.ng-enter {
            .transition-delay(220ms);
            opacity: 0;
            .translate3d(96px, 0, 0);
        }

        &.ng-enter-active {
            opacity: 1;
            .translate3d(0, 0, 0);
        }

        &.ng-leave {
            .translate3d(0, 0, 0);
            opacity: 1;
        }

        &.ng-leave-active {
            transform: scale(.95);
            opacity: 0;
        }
    }

    &.init {

        [ng-view].ng-enter {
            .translate3d(0, 96px, 0);
            opacity: 0;
        }

        [ng-view].ng-enter-active {
            opacity: 1;
            .translate3d(0, 0, 0);
        }
    }
}
