// Text manipulation
.ellipsis, [ellipsis] {
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
}

// Cursor
.noselect, [noselect] {
    -webkit-touch-callout: none;
    -webkit-user-select: none;
    -khtml-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
}
.cursor, [cursor] {
    cursor: pointer;
}
.cursor-default, [cursor-default] {
    cursor: default;
}
.touch-overflow, [touch-overflow] {
    -webkit-overflow-scrolling: touch;
}

.block, [block]{
    display: block;
}
.nopadding, [nopadding] {
    padding: 0;
}
.nomargin, [nomargin] {
    margin: 0;
}

// Text manipulation
.text-center, [text-center] {
    text-align: center;
}
.text-left, [text-left] {
    text-align: left;
}
.text-right, [text-right] {
    text-align: right;
}
.text-uppercase, [text-uppercase] {
    text-transform: uppercase;
}
.text-capitalize, [text-capitalize] {
    text-transform: capitalize;
}
.text-lowercase, [text-lowercase] {
    text-transform: lowercase;
}

// Positioning
.absolute, [absolute] {
    position: absolute;
}
.relative, [relative] {
    position: relative;
}
.fixed, [fixed] {
    position: fixed;
}
.topRight(@top, @right) {
    top: @top;
    right: @right;
}
.spread, [spread] {
    .absolute();
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
}
.flexbox, [flexbox] {
    .flex-block();
}
.flexwrap, [flexwrap] {
    .flexwrap();
}
.fwidth, [fwidth] {
    width: 100%;
}
.boxShadow, [box-shadow] {
    .box-shadow(0 0 25px rgba(0, 0, 0, .11), 0 0 0 1px rgba(0, 0, 0, .03));
}
// Form manipulation
.rounded, [rounded] {
    border-radius: 50%;
}
.square(@dim) {
    width: @dim;
    height: @dim;
}
